.timer-container {
  justify-content: center;
  justify-self: center;

  text-align: center;
  color: white;

  width: 250px;
  height: 250px;

  padding: 20px;
  margin-top: 5vh;

  user-select: none;
}

.timer-mode {
  font-size: 20px;
  font-weight: bold;
}

.timer-time {
  font-size: 36px;
  margin: 20px 0px 20px 0px;
}

.pomodoro-counter {
  margin-top: 15px;
  margin-bottom: 20px;
}

.badge-btn-restarter:hover {
  cursor: pointer;
  background-color: rgb(9, 163, 9);
}

.timer-buttons button {
  margin: 5px;
  padding: 1px;
}

.pomodoro-cycle-bar-container {
  width: 100%;
  height: 20px;

  background-color: rgb(185, 0, 0);

  border-radius: 20px;

  margin: 5vh 0 5vh 0;
}

.pomodoro-cycle-bar {
  height: 20px;
  border-radius: 20px;
  background-color: rgb(10, 151, 10);
}

.pomodoros-till-long-break {
  visibility: visible;

  color: #9c9c9c;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-style: italic;
  font-weight: bolder;
}

@media only screen and (max-height: 500px) {
  .pomodoros-till-long-break {
    visibility: hidden;
  }
}